//  
function Modal(props) {
  return (
    <div className='modal' style={{ background: props.color }}>
      <h4>{props.title[props.viewTitle]}</h4>
      <p></p>
      <p></p>
      <button onClick={() => {
        let copy = [...props.title];
        copy[0] = ' ';
        props.setTitle(copy);
      }}></button>
    </div >
  )
}

class Modal2 extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      name: 'kim',
      age: 20,
    }
  }
  render() {
    return (
      <div> {this.state.age}
        <button onClick={() => {
          this.setState({ age: 21 })
        }}></button>
      </div>
    )
  }
}



export default App;

